import JSZip from 'jszip';
import parseInf from './InfParser.js';

export default class ZipRepo {
    loadZipRepo( buf ){
	let ok, nok;

	let p = new Promise( (_ok, _nok ) => {
	    ok = _ok;
	    nok = _nok;
	});
	    
	JSZip.loadAsync( buf )
	    .then( z => {
		let hexRefs = Object.keys(z.files).map( f => z.files[f] ).filter( f => !f.dir && /\.HEX$/.test(f.name) );
		Promise.all( hexRefs.map( file => file.async("text") ) )
		    .then( hexes => {

			let infRefs = Object.keys(z.files).map( f => z.files[f] ).filter( f => !f.dir && /\.INF$|\.JSON$/.test(f.name) );
			Promise.all( infRefs.map( file => file.async("arraybuffer") ) )
			    .then( infs => {

				let hexIndex;
				hexIndex = hexRefs.reduce( (acc, ref, i) => {
				    acc[ ref.name.replace(/\.HEX$/, '') ] = hexes[i];
				    return acc;
				}, {} );

				let items = infRefs.map( (infRef, i) => {
				    let ext = infRef.name.split(".").pop();
				    let name = infRef.name.replace(/\.(INF|JSON)$/, '');
				    let hex = hexIndex[name];
				    if( !hex ) return null;
				    if( ext == "INF" )
					return parseInf( name, new Uint8Array(infs[i]), hex, "application/x-hex" );
				    else{
					let json = {device:"arduboy"};
					try{
					    let td = new TextDecoder();
					    json = JSON.parse(td.decode(infs[i]));
					}catch(ex){}
					return Object.assign({ raw:hex, rawType:"application/x-hex" }, json);
				    }
				});

				ok( items );
				
			    });

		    });

	    });

	return p;
    }

}



